/*
*  Here are global styles, they do things everywhere
*/

img {
  width: 100%;
}

h1, h2, h3, h4, p, a {
  margin-top: 0;
}

a {
  font-family: $f-special;
  font-size: 12px;
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

p {
  font-family: $f-default;
  font-size: 12px;
  line-height: 1.3;
}

h1 {
  font-family: $f-special;
  font-size: 32px;
  font-weight: bold;
}

h2 {
  font-family: $f-special;
  font-size: 26px;
  font-weight: bold;
}

h3 {
  font-family: $f-special;
  font-size: 20px;
  font-weight: bold;
}

h4 {
  font-family: $f-special;
  font-size: 16px;
  font-weight: bold;
}

.button {
  background: $c-button-background;
  border: 2px solid $c-button-border;
  cursor: pointer;
  display: inline-block;
  font-family: $f-special;
  font-weight: bold;
  font-size: 16px;
  height: 30px;
  margin-bottom: 10px;
  padding: 0 30px;
  text-transform: uppercase;

  p {
    line-height: 30px;
    margin: 0
  }

  &:hover {
    background: lighten($c-button-background, 10);
  }
}