.page-container {
  display: flex;
  flex-flow: column;
}
.top-header {
  width: 100%;
  height: 200px;
  background-color: red;
  display: inline-block;
}

.content-container {
  padding: $small-trim 0;
  @include container-margin;
}

/* ----- Project collection layout ------
      .project-collection
        .project-item (featured)
          .project-item-image
          .project-item-content
*/

.project-collection {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.project-item {
  flex: 1 1 100%;
  border: 3px solid green;
  cursor: pointer;
  margin-bottom: 20px;

  h1 {
    text-align: center;
  }

  &:hover {
    @include hoverBorder;
  }
}

.project-item.featured {
  width: 100%;
}

@media screen and (min-width: $bp-small) {

  .project-item {
    display: flex;
    flex: 1 1 45%;

    &:nth-of-type(2n) {
      margin-right: 5%;
    }
  }

  .project-item.featured {
    flex: 1 1 100%;
  }
}

/* ----- Landing Item Layout ------
  .item-landing-template
    .landing-item
      .landing-image
      .landing-actions
        .actions-container
      .landing-content
*/

.landing-item {
  display: flex;
  flex-flow: row nowrap;
  flex: 1 1 100%;
  border: 3px solid green;
}

.landing-image {
  flex: 1 1 40%;
}

.landing-actions {
  flex: 1 1 25%;
  text-align: center;
}

.actions-container {
  display: flex;
  width: 70%;

  .button {
    width: 100%;
  }
}

.landing-content {
  flex: 1 1 35%;
}

@media screen and (min-width: $bp-small) {
  
}


